import { Component, ChangeDetectorRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading, AlertController, Content } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ToolsProvider } from '../../providers/tools/tools';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivityServiceProvider } from '../../providers/activity-service/activity-service';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { StripeService, Elements, Element as StripeElement, ElementsOptions } from "ngx-stripe";
import { DeviceServiceProvider } from '../../providers/device-service/device-service';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { NotificationProvider } from '../../providers/notification/notification';

/**
 * Generated class for the ActivityPaymentPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-activity-payment',
  templateUrl: 'activity-payment.html',
})
export class ActivityPaymentPage {
  @ViewChild(Content) content: Content;
  loading: Loading; // for loader
  paymentForm: FormGroup; // payment form initialise
  cancellationTitle: any; // for cancellation title
  acceptTerms: any; // accept terms html content
  cancellationText: any; // cancellation html content
  chargeText: boolean = false; // for payment type
  item: any; //to store activity service
  elements: Elements; // for credit card validation with stripe
  card: StripeElement; // stripe card
  isChecked: boolean = false; // accept terms radio check
  lang: any = this.deviceProvider.preferredLanguage;
  hideBackButton: boolean = false;
  elementsOptions: ElementsOptions = {
    locale: this.lang
  };

  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder, private toolsProvider: ToolsProvider, public sanitized: DomSanitizer, private activityProvider: ActivityServiceProvider, private reservationProvider: ReservationServiceProvider, public loadingCtrl: LoadingController, private stripeService: StripeService, private deviceProvider: DeviceServiceProvider, private alertCtrl: AlertController, private commonProvider: CommonServiceProvider, public cdRef: ChangeDetectorRef, private notificationProvider: NotificationProvider) {
    this.paymentForm = formBuilder.group({
      'cardHolder': new FormControl('', [Validators.required]),
      'acceptTermsCheck': new FormControl(null, [Validators.required])
    });

  }
  
  ionViewWillEnter(){
    this.hideBackButton = false;
  }

  ionViewDidLoad() {
    this.showLoading();
    let me = this;
    this.item = this.navParams.get('item'); // gets item 
    this.hideBackButton = false;
    // payment type check
    if (this.item.paymentType == "GUARANTEE") {
      this.chargeText = false;
    } else {
      this.chargeText = true;
    }
    // stripe implementation
    this.stripeService.elements(this.elementsOptions)
      .subscribe(elements => {
        this.elements = elements;
        // Only mount the element the first time
        if (!this.card) {
          this.card = this.elements.create('card', {
            style: {
              base: {
                iconColor: '#666EE8',
                color: '#31325F',
                lineHeight: '40px',
                fontWeight: 300,
                fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
                fontSize: '15px',
                '::placeholder': {
                  color: '#CFD7E0'
                }
              }
            }
          });
          this.card.mount('#card-element');
        }
      });

    let reservationId = this.reservationProvider.objReservation.reservationId; //reservation id
    //translate cancellation text and accept terms
    let res = this.toolsProvider.fnLanguageTranslate(['view.reservation.label.CancellationTermsTitle', 'view.reservation.label.acceptTerms']);
    res.subscribe((value: string) => {
      this.cancellationTitle = this.sanitized.bypassSecurityTrustHtml(value['view.reservation.label.CancellationTermsTitle']);
      this.acceptTerms = this.sanitized.bypassSecurityTrustHtml(value['view.reservation.label.acceptTerms']);
      this.cdRef.detectChanges(); // Checks the view and its children.
      /* click binding to termsofservice span element and redirection */
      let termsElmnt = document.querySelector('ion-nav').querySelectorAll("#termsOfService");
      for (let i = 0; i < termsElmnt.length; i++) {
        if (termsElmnt[i]) {
          termsElmnt[i].addEventListener('click', function (e) {
            me.navCtrl.push("TermsAndConditionsPage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
          });
        }
      }

      // get cancellation policy text
      this.commonProvider.getCancellationPolicy(reservationId).subscribe(data => {
        this.loading.dismiss();
        if (data.success == true) {
          this.cancellationText = data.response;
        }

      });

    });
    this.fnCheckPreEnteredFormData();
  }

  // store entered data into model before leaving page
  public ionViewWillLeave() {
    if (this.commonProvider.objFormData.activity.bookingCompleted != true) {
      this.commonProvider.objFormData.activity.cardHolder = this.paymentForm.get('cardHolder').value;
      this.commonProvider.objFormData.activity.agreeTerms = this.isChecked;

    }
    this.hideBackButton = true;
  }

  // fetch pre-entered data into fields from model
  fnCheckPreEnteredFormData() {
    if (this.commonProvider.objFormData && this.commonProvider.objFormData.activity) {
      this.commonProvider.objFormData.activity.bookingCompleted = false;
      if (this.commonProvider.objFormData.activity.id == this.item.id) {
        this.paymentForm.get("cardHolder").setValue(this.commonProvider.objFormData.activity.cardHolder);
        this.isChecked = this.commonProvider.objFormData.activity.agreeTerms;
      }
    }
  }
  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

  public fnSubmit() {

    if (this.paymentForm.controls['cardHolder'].hasError('required')) { // if cardholder is empty
      let validateMessage1 = this.toolsProvider.fnLanguageTranslate(['view.reservation.label.cardHolderEmpty', "common.error", "buttons.ok"]);

      validateMessage1.subscribe((value: string) => {
        let alertnew1 = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.label.cardHolderEmpty'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew1.present();
        setTimeout(() => { alertnew1.dismiss(); }, 5000);
      });
    } else if (this.isChecked == false) { // if agree terms not checked
      let validateMessage2 = this.toolsProvider.fnLanguageTranslate(['view.reservation.label.mustAgreeTerms', "common.error", "buttons.ok"]);

      validateMessage2.subscribe((value: string) => {
        let alertnew2 = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.reservation.label.mustAgreeTerms'],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew2.present();
        setTimeout(() => { alertnew2.dismiss(); }, 5000);
      });
    } else {
      // service call
      if (this.card != null && this.card != undefined) {
        this.showLoading();
        //create stripe token
        this.commonProvider.objServiceForm.cardHolder = this.paymentForm.get('cardHolder').value;
        let name = this.paymentForm.get('cardHolder').value;
        this.stripeService.createToken(this.card, { name }).subscribe(result => {

          if (result.token) {

            this.commonProvider.objServiceForm.token = result.token.id;
            this.commonProvider.objServiceForm.tokenArray = result.token;
            this.loading.dismiss();
            let confirmMessages = this.toolsProvider.fnLanguageTranslate(['view.reservation.confirm', "common.confirm", "buttons.modify", "buttons.send"]);

            confirmMessages.subscribe((value: string) => {
              let alertconfirm = this.alertCtrl.create({
                title: value['common.confirm'],
                message: value['view.reservation.confirm'],
                cssClass: 'cls_alrt',
                buttons: [
                  {
                    text: value['buttons.modify'],
                    role: 'cancel',

                  },
                  {
                    text: value['buttons.send'],
                    role: 'cancel',
                    handler: data => {
                      this.showLoading();
                      let serviceId = this.commonProvider.objServiceForm.serviceId,
                        date = this.commonProvider.objServiceForm.date,
                        turn = this.commonProvider.objServiceForm.turn,
                        noOfPersons = this.commonProvider.objServiceForm.noOfPersons,
                        comments = this.commonProvider.objServiceForm.comments,
                        cardholder = this.paymentForm.get('cardHolder').value,
                        token = result.token.id,
                        reservationId = this.reservationProvider.objReservation.reservationId;
                      // service call for reservation booking with payment
                      this.activityProvider.sendServiceReservationWithPayment(reservationId, serviceId, date, turn, noOfPersons, comments, cardholder, token).subscribe(data => {
                        this.loading.dismiss();
                        if (data.success == true) {

                          this.commonProvider.objFormData.activity = [];
                          this.commonProvider.objFormData.activity.bookingCompleted = true;
                          // success message translate
                          let successMessages = this.toolsProvider.fnLanguageTranslate(['view.reservation.success', "common.requestReceived", "buttons.ok"]);

                          successMessages.subscribe((value: string) => {
                            let alertsuccess = this.alertCtrl.create({
                              title: value['common.requestReceived'],
                              message: value['view.reservation.success'],
                              cssClass: 'cls_alrt',
                              buttons: [
                                {
                                  text: value['buttons.ok'],
                                  role: 'cancel',
                                  handler: data => {
                                    this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                                  }
                                }]
                            });
                            alertsuccess.present();
                            setTimeout(() => {
                              alertsuccess.dismiss();
                              this.navCtrl.popTo(this.navCtrl.getByIndex(1), { animate: true, animation: 'transition', duration: 500, direction: 'back' });
                            }, 5000);
                          });
                        }


                      });
                    }
                  }]
              });
              alertconfirm.present();
              setTimeout(() => { alertconfirm.dismiss(); }, 5000);
            });




          } else if (result.error) {
            // Error creating the token
            console.log(result.error.message);
            this.loading.dismiss();
            // error message translate
            let errorMessages = this.toolsProvider.fnLanguageTranslate([result.error.message, "common.error", "buttons.ok"]);

            errorMessages.subscribe((value: string) => {
              let alertsuccess = this.alertCtrl.create({
                title: value['common.error'],
                message: value[result.error.message],
                cssClass: 'cls_alrt',
                buttons: [
                  {
                    text: value['buttons.ok'],
                    role: 'cancel',

                  }]
              });
              alertsuccess.present();
              setTimeout(() => { alertsuccess.dismiss(); }, 5000);
            });
          }

        });

      }
    }
  }

  public fnOnRadioSelect($event) {
    this.isChecked = !this.isChecked;
  }

  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

}



